<template>
<b-card no-body>
<b-tabs card v-model="tabIndex">
    <b-tab title="Ingrese Certificado" :disabled="disabled[0]">
        <myForm  :FormDefinition="certificado" v-model="form" @submit.prevent="onSubmit" @error="onError" :disabled="disabled[0]"/>
    </b-tab>
    <b-tab title="Seleccione Empresa" :disabled="disabled[1]">
    </b-tab>
    <b-tab title="Seleccione Facturas" :disabled="disabled[2]">
    </b-tab>
  </b-tabs>
  </b-card>
</template>

<script>

export default {
    name: 'Cesion',
    data () {
        return {
            tabIndex:0,
            disabled:[false,true,true],
            form:{
                pfx:null,
            },
            certificado:{
                "submit":{
                    href:"any.html",
                    value:"Subir"
                },
                "inputs":[
                    {
                        "type":"file",
                        "name":"pfx",
                        "label":"Certificado",
                        "description":"Agregue su certificado",
                        "accept":"application/pkcs12",
                        "required":true
                    }
                ]
            }
        }
    },
    methods:{
        onSubmit(evt){
            //evt.preventDefault();
            console.log(this.form);
            this.disabled[1]=false;
            this.$nextTick(()=>{
                this.tabIndex++;
            });
            /*
            setTimeout(()=>{
                this.tabIndex++
                this.disabled[0]=true;
            },100);
            */
        },
        onError(text){
            alert(text);
        }
    },
    mounted(){
        console.log("ready");
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



